import React, {useState} from "react";
import { Link } from "react-router-dom";
import OutsideClickHandler from "react-outside-click-handler";

import { Nav } from "./NavHome";
import BackHeader from "./BackHeader";
import { ModalMatch } from "./Modal";

const matchNow = true; //    
// const matchNow = false; //    

const matchPast = true; //    
// const matchPast = false; //    

function PastMatch({data}){
    const [toggle, setToggle] = useState(false)

    return(
        <div className="past-match">
            <div className="number">
                <p>NO. 2021082001</p>
                {
                    toggle
                    ?
                    <p onClick={()=>{setToggle(!toggle)}}><img src={require('./../images/icon_up.png').default} alt="icon_up"></img></p>
                    :
                    <p onClick={()=>{setToggle(!toggle)}}><img src={require('./../images/icon_specific.png').default} alt="icon_specific"></img></p>
                }
            </div>
            <h2>2021.08.20()
                {
                    data.pass === ' '
                    ?
                    <span> </span>
                    :
                    <span style={{color : '#333'}}>{data.pass}</span>
                }
            </h2>
            <table>
                <tbody>
                    {
                        toggle &&
                        <>
                            <tr className="match-toggle-top">
                                <th> </th>
                                <td></td>
                            </tr>
                            <tr>
                                <th></th>
                                <td>2021-08-20 10:50:40</td>
                            </tr>
                            <tr>
                                <th></th>
                                <td>010-1234-****</td>
                            </tr>
                            <tr>
                                <th></th>
                                <td>  10 3</td>
                            </tr>
                            <tr className="match-toggle">
                                <th> </th>
                                <td>-</td>
                            </tr>
                        </>             
                    }


                    <tr className="match-info-top">
                        <th> </th>
                        <td>LG </td>
                    </tr>
                    <tr>
                        <th> </th>
                        <td>2022.05.31</td>
                    </tr>
                    <tr>
                        <th> </th>
                        <td>84,500</td>
                    </tr>                         

                    {
                        data.reason &&
                        (
                            toggle &&
                            <>
                                <tr className="toggle-style">
                                    <th></th>
                                    <td></td>
                                </tr>
                                <tr className="match-cancle">
                                    <th></th>
                                    <td>{data.reason}</td>
                                </tr>
                            </>
                        )
                    }
                                                                               
                </tbody>
            </table>                
        </div>
    )
}

function MiniModal({setModalMatch, closeModal}){
    return(
        <OutsideClickHandler onOutsideClick={()=>{closeModal(false)}}>
            <div className="mini-modal">
                <p onClick={()=>{setModalMatch(true)}}> </p>
                <Link to="/nav/match/info"><p> </p></Link>
            </div>
        </OutsideClickHandler>
    )
}

function MatchBox({setModalMatch}){
    const [openModal, setOpenModal] = useState(false);

    return(
        <>
        <div className="match-box" onClick={()=>{setOpenModal(true)}}>
            <h2>LG .</h2>
            <p className="match-name"> | <span></span>  6</p>
            <p className="match-check">  <span>   .</span></p>
            
            {openModal && <MiniModal setModalMatch={setModalMatch} closeModal={setOpenModal}></MiniModal>}
        </div>
        </>
    )
}

function NavMatch(){
    const [toggleState, setToggleState] = useState(1);
    const [modalMatch, setModalMatch] = useState(false);

    const toggleTab = (index) => {
        setToggleState(index);
    };

    const matchData = [
        {
            id:1,
            pass:' ',
        },
        {
            id:2,
            pass:' ',
            reason:'        '
        }
    ]

    const nav = [
        {
            id:1,
            link:'home',
            img:'home',
            name:'',
        },
        {
            id:2,
            link:'heart',
            img:'heart',
            name:''
        },
        {
            id:3,
            link:'match',
            img:'match_on',
            name:''
        },
        {
            id:4,
            link:'mypage',
            img:'mypage',
            name:''
        }
    ]

    return(
        <div className="matchPage AppIn">
            <BackHeader data={''} back={'/nav/home'}></BackHeader>

            <div className="container">
                <div className="bloc-tabs">
                    <button
                    className={toggleState === 1 ? "tabs active-tabs" : "tabs"}
                    onClick={() => toggleTab(1)} 
                    >
                     
                    </button>
                    <button
                    className={toggleState === 2 ? "tabs active-tabs" : "tabs"}
                    onClick={() => toggleTab(2)}
                    >
                      
                    </button>
                </div>

                <div className="content-tabs">

                    <div
                    className={toggleState === 1 ? "content  active-content" : "content"}
                    >
                    {
                    matchNow
                    ?                        
                        <div className="matchBox-wrap">
                            <MatchBox setModalMatch={setModalMatch}></MatchBox>
                            <MatchBox setModalMatch={setModalMatch}></MatchBox>
                            <MatchBox setModalMatch={setModalMatch}></MatchBox>
                            <MatchBox setModalMatch={setModalMatch}></MatchBox>
                            <MatchBox setModalMatch={setModalMatch}></MatchBox>
                            <MatchBox setModalMatch={setModalMatch}></MatchBox>
                            <MatchBox setModalMatch={setModalMatch}></MatchBox>
                            <MatchBox setModalMatch={setModalMatch}></MatchBox>
                            <MatchBox setModalMatch={setModalMatch}></MatchBox>
                            <MatchBox setModalMatch={setModalMatch}></MatchBox>
                            <MatchBox setModalMatch={setModalMatch}></MatchBox>
                        </div>
                    :
                        <p className="match-null">  .</p>
                    }                        
                    </div>

                    <div
                    className={toggleState === 2 ? "content  active-content" : "content"}
                    >
                    {
                    matchPast
                    ?                        
                        <div className="past-wrap">
                            {matchData.map(data => (
                                <PastMatch data={data} key={data.id}></PastMatch>
                            ))}
                        </div>
                    :
                        <p className="match-null">  .</p>
                    }                        
                    </div>

                </div>
            </div>
            { modalMatch && <ModalMatch closeModal={setModalMatch}/> }

            <nav>
                <div className="nav-wrap">
                    {nav.map(data => (
                        <Nav nav={data} key={data.id} />
                    ))}
                </div>
            </nav>
        </div>
    )
}

export default NavMatch;