import React, {Component} from "react";
import BackHeader from "./BackHeader";
class AlertHeader extends Component{
    render(){
        return(
            <div className="alert-header">
                <h2>{this.props.data}</h2>
                <div></div>
            </div>
        )
    }
}

function AlertBox({data, prev}){
    return(
        <div className="alert-box" style={data.new}>
            <p>{data.name}</p>
            <p>{data.time}{prev}</p>
        </div>
    )
}

function HomeAlert(){
    const alertToday = [
        {
            id:1,
            name: '[]    ',
            time: '18:21',
            new: {border: '1px solid rgba(0, 159, 226, 0.5)'}
        },
        {
            id:2,
            name: '[1:1 ]  .',
            time: '21:45',
        }
    ]

    const alertPrevious = [
        {
            id:1,
            name: '[]   .    .',
            time: '1'
        }
    ]

    return(
        <div className="alertPage">
            <BackHeader data={''} back={'/nav/home'}></BackHeader>
            <div className="alert-list AppIn">
                <AlertHeader data={' '}></AlertHeader>
                {alertToday.map(data => (
                    <AlertBox data={data} key={data.id} />
                ))}

                <AlertHeader data={' '}></AlertHeader>
                {alertPrevious.map(data => (
                    <AlertBox data={data} key={data.id} prev={''}/>
                ))}
            </div>
        </div>
    )
}

export default HomeAlert;