import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { BlueFlat } from '../BtnSrc';

export function ModalMatch({ closeModal }) {
  const [nextModal, setNextModal] = useState(false);
  const [complete, setComplete] = useState(false);

  return nextModal ? (
    complete ? (
      <div className='modal-background'>
        <div className='modal'>
          <img
            src={require('./../images/modal_check.png').default}
            alt='modal'></img>
          <h2>
            <span> </span> 
            <br />
            <span></span>.
          </h2>
          <p>   .</p>
          <div>
            <div
              onClick={() => {
                closeModal(false);
              }}>
              <BlueFlat data={' '}></BlueFlat>
            </div>
          </div>
        </div>
      </div>
    ) : (
      <div className='modal-background'>
        <div className='modal'>
          <h2> .</h2>
          <textarea placeholder=' .'></textarea>
          <div>
            <div className='gray-flat' onClick={() => closeModal(false)}>
              
            </div>
            <div
              onClick={() => {
                setComplete(true);
              }}>
              <BlueFlat data={''}></BlueFlat>
            </div>
          </div>
        </div>
      </div>
    )
  ) : (
    <div className='modal-background'>
      <div className='modal'>
        <h2 className='mb16'> ?</h2>
        <div>
          <div className='gray-flat' onClick={() => closeModal(false)}>
            
          </div>
          <div
            onClick={() => {
              setNextModal(true);
            }}>
            <BlueFlat data={''}></BlueFlat>
          </div>
        </div>
      </div>
    </div>
  );
}

export function ModalComplete() {
  return (
    <div className='modal-background'>
      <div className='modal'>
        <img
          src={require('./../images/modal_check.png').default}
          alt='modal'></img>
        <h2>
          <span>  </span> 
          <br />
          <span></span>.
        </h2>
        <p>   .</p>
        <div>
          <Link to='/nav/match'>
            <BlueFlat data={' '}></BlueFlat>
          </Link>
        </div>
      </div>
    </div>
  );
}

