import React, { Component, useState } from 'react';
import { Link } from 'react-router-dom';
import OutsideClickHandler from 'react-outside-click-handler';

const bellNew = true; //   
// const bellNew = false; //   

function MiniModal({ closeModal }) {
  return (
    <OutsideClickHandler
      onOutsideClick={() => {
        closeModal(false);
      }}>
      <div className='mini-modal'>
        <p> </p>
        <p></p>
      </div>
    </OutsideClickHandler>
  );
}

export function HomeList({ user }) {
  const [modal, setModal] = useState(false);

  return (
    <div className='home-list'>
      <img
        src={require('./../images/list' + user.id + '.png').default}
        alt='list img'></img>
      <div className='list-text'>
        <div className='list-title'>
          <h2>{user.name}</h2>
          <div
            onClick={() => {
              setModal(true);
            }}>
            {user.edit}
          </div>
        </div>
        <p className='list-address'>
          {user.address} |    {user.months}
        </p>
        <span></span>
        <div className='list-bottom'>
          <p> {user.price}</p>
          <div className='icon-wrap'>
            <div>
              <img
                src={require('./../images/list_likes.png').default}
                alt='list_likes'></img>
              <p>{user.likes}</p>
            </div>
            <div>
              <img
                src={require('./../images/list_comment.png').default}
                alt='list_comment'></img>
              <p>{user.comment}</p>
            </div>
          </div>
        </div>
      </div>

      {modal && <MiniModal closeModal={setModal}></MiniModal>}
    </div>
  );
}

export function Nav({ nav }) {
  return (
    <Link to={nav.link}>
      <div className='nav'>
        <img
          src={require('./../images/nav_' + nav.img + '.png').default}
          alt='nav icon'></img>
        <h2>{nav.name}</h2>
      </div>
    </Link>
  );
}

export class SearchBox extends Component {
  render() {
    return (
      <div className='search-box'>
        <input type='search' placeholder={this.props.holder}></input>
      </div>
    );
  }
}

